import { AchievementState } from './../../shared/models/achievement.model';
import { RouterConstants } from './../../shared/config/constants/router.constants';
import { Router } from '@angular/router';
import { Achievement } from 'src/app/shared/models/achievement.model';
import { AchievementService } from './services/achievement.service';
import { Component, OnInit } from '@angular/core';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-achievement-list',
  templateUrl: './achievement-list.page.html',
  styleUrls: ['./achievement-list.page.scss'],
})
export class AchievementListPage implements OnInit {

  achievements: Array<Achievement> = new Array<Achievement>();
  totalElements: number;
  loadingContent: boolean;
  currentElement: number;
  elementsPerPage: number = 10;

  AchievementState = AchievementState;

  environment = environment;
  constructor(
    public globalData: GlobalDataService,
    private achievementService: AchievementService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getAchievements();
  }

  array(n: number): any[]{
    return Array(n);
  }

  async getAchievements(event?) {
    try {
      if(!event) this.loadingContent = true;
      this.currentElement = this.achievements.length;
      const data = await this.achievementService.getChallenges(this.currentElement, this.elementsPerPage).toPromise();
      this.totalElements = data.count;
      this.achievements = this.achievements.concat(data.rows);
      console.log('Achievements ', this.achievements, this.totalElements);
      if (event) { await event.target.complete(); }
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  async refreshData(event) {
    this.achievements = Array<Achievement>();
    await this.getAchievements()
    event.target.complete();
  }

  async openDetail(element) {
    this.router.navigateByUrl(RouterConstants.ACHIEVEMENT_DETAIL+ element.id, {state: element.selectedElement});
  }


}
